import React from 'react';
import { useSelector } from 'react-redux';
import { TRootState } from '../../services/reducers';
import OrdersList from '../orders-list/orders-list';
import styles from './feed-list.module.css';

const FeedList: React.FC = () => {
  const { orders, isFetchingFeed, isErrorWhileFetchingFeed } = useSelector(
    (state: TRootState) => state.feed,
  );

  return (
    <div className={styles.container}>
      <p className="text text_type_main-large mb-10 mt-15"> </p>
      {isFetchingFeed ? <div> ...</div> : <OrdersList orders={orders} />}

      {isErrorWhileFetchingFeed && <div>   </div>}
    </div>
  );
};

export default FeedList;
